









































import Vue from 'vue';

export default Vue.extend({
  data: () => ({
    item: 1,
    search: '',
    items: [
      { id: 1, title: 'Таштагольский рудник 1', icon: 'delete' },
      { id: 2, title: 'Таштагольский рудник 2', icon: 'delete' },
      { id: 3, title: 'Таштагольский рудник 3', icon: 'delete' },
    ],
    checked: [] as number[],
    selectedAll: false,
    sequence: 3,
  }),
  computed: {
    filteredList() {
      if (this.search) {
        return this.items.filter(shop => (
          shop.title.toLowerCase().includes(this.search.toLowerCase())
        ));
      }
      return this.items;
    },
    canRemoveChecked(): boolean {
      return !!this.checked.length;
    },
    canAddShop(): boolean {
      return !!this.search;
    },
  },
  methods: {
    selectAll() {
      this.checked = [];
      if (this.selectedAll) this.filteredList.map(item => this.checked.push(item.id));
    },
    select() {
      this.selectedAll = true;
      if (this.checked.length === 0) this.selectedAll = false;
    },
    removeItem(idx: number) {
      Vue.delete(this.items, idx);
      this.checked = [];
    },
    removeCheckedItems() {
      const filtered = this.filteredList.filter(item => !this.checked.includes(item.id));
      this.items = filtered;
      this.checked = [];
      this.selectedAll = false;
    },
    addItem() {
      if (this.search) {
        this.sequence += 1;
        this.items.push({ id: this.sequence, title: this.search, icon: 'delete' });
        this.search = '';
      }
    },
  },
});
